/**
 * 一些坐标获取的方法
 * @author riverhan
 * @date 2016-8-12
 */
const body = document.body;

/**
 * 获取DOM元素相对于body的left值
 * @param  {Dom} element 目标元素
 * @return {Number}      left值
 */
export function getElementLeft(element) {
  let actualLeft = element.offsetLeft;
  let current = element.offsetParent;

  while (current && current !== body) {
    actualLeft += current.offsetLeft;
    current = current.offsetParent;
  }

  return actualLeft;
}

/**
 * 通过getBoundingClientRect获取DOM元素相对于body的left值
 * @param  {Dom} element 目标元素
 * @return {Number}      left值
 */
export function getElementLeftRect(element) {
  let bodyRect = body.getBoundingClientRect();
  let elemRect = element.getBoundingClientRect();

  return elemRect.left - bodyRect.left;
}

/**
 * 获取DOM元素相对于body的top值
 * @param  {Dom} element 目标元素
 * @return {Number}      top值
 */
export function getElementTop(element) {
  let actualTop = element.offsetTop;
  let current = element.offsetParent;

  while (current && current !== body) {
    actualTop += current.offsetTop;
    current = current.offsetParent;
  }

  return actualTop;
}

/**
 * 通过getBoundingClientRect获取DOM元素相对于body的top值
 * @param  {Dom} element 目标元素
 * @return {Number}      left值
 */
export function getElementTopRect(element) {
  let bodyRect = body.getBoundingClientRect();
  let elemRect = element.getBoundingClientRect();

  return elemRect.top - bodyRect.top;
}
